.btn {
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
}

.btn.white {
    /* background: rgba(255, 255, 255, 0.274); */
    background: #212121;
    border: 1px solid rgba(255, 255, 255, 1);
    box-shadow: 0 0 3.2px rgba(255, 255, 255, 1);
    border-radius: 8px;
}

